import React from 'react'
import { Button, Form, Grid, Header, Image, Loader, Message, Segment, Select } from 'semantic-ui-react'
import validator from 'validator'
import { SERVER_API } from '../../../config'
import { anonymousCallWithData } from "../../../utils/ApiUtils";
import { withRouter } from 'react-router-dom'


const heightStyle = {
    height: '80vh'
}
const widthStyle = {
    maxWidth: 450
}

const roleOptions = [
    {
        key: 'parent',
        value: 'parent',
        text: 'Ph huynh'
    },
    {
        key: 'user',
        value: 'user',
        text: 'Hc sinh'
    }
]

class RegisterScreen extends React.Component {
    state = {
        email: '',
        password: '',
        rePassword: '',
        name: '',
        phone: '',
        role: 'user',
        formError: '',
        formSuccess: false,
        loading: false,
        redirectLoading: false
    }

    constructor(props) {
        super(props)
        this.handleChange = this.handleChange.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }

    handleChange(e, { name, value }) {
        this.setState({
            [name]: value
        })
    }

    registerClick() {
        this.setLoading(true);
        const { name, email, password, phone, role } = this.state
        anonymousCallWithData(
            'POST',
            `${SERVER_API}/signup`,
            {
                name,
                email,
                password,
                phone,
                role
            },
            data => {
                this.setState({
                    formSuccess: true
                },
                    () => {
                        this.setState({
                            redirectLoading: true
                        })
                        this.timeout = setTimeout(() => {
                            const { history } = this.props
                            history.push('/')
                        }, 2000)
                    })
            },
            err => this.setError(err),
            err => this.setError(err),
            () => this.setLoading(false)
        )
    }
    setLoading(loading) {
        this.setState({
            loading
        })
    }
    setError(err) {
        this.setState({
            formError: err
        })
    }

    componentDidMount() {
        clearTimeout(this.timeout)
    }

    onSubmit() {
        if (!validator.isEmail(this.state.email)) {
            this.setState({
                formError: 'Email khng hp l!'
            })
            return
        }
        if (this.state.password !== this.state.rePassword) {
            this.setState({
                formError: 'Mt khu khng khp.'
            })
            return
        }
        this.setState({
            formError: ''
        })
        this.registerClick()
    }

    render() {
        const { formSuccess, formError, loading, email, password, rePassword, phone, name } = this.state
        return (
            <div>
                <Grid textAlign='center' style={heightStyle} verticalAlign='middle'>
                    <Grid.Column style={widthStyle}>
                        <Header as='h2' color='teal' textAlign='center'>
                            <Image src='https://www.digicert.com/account/images/login-shield.png' /> ng k ti khon
                        </Header>
                        <Form size='large' error={formError} loading={loading}
                            success={formSuccess}>
                            <Segment stacked>
                                <Message
                                    error
                                    attached
                                    header="Error"
                                    content={this.state.formError}
                                    style={{ marginBottom: '10px' }}
                                />
                                <Form.Input
                                    required
                                    fluid
                                    icon='user'
                                    iconPosition='left'
                                    placeholder='Email'
                                    name='email'
                                    value={email}
                                    onChange={this.handleChange}
                                />
                                <Form.Input
                                    required
                                    fluid
                                    icon='lock'
                                    iconPosition='left'
                                    placeholder='Mt khu'
                                    type='password'
                                    name='password'
                                    value={password}
                                    onChange={this.handleChange}
                                />
                                <Form.Input
                                    required
                                    fluid
                                    icon='lock'
                                    iconPosition='left'
                                    placeholder='Nhp li mt khu'
                                    type='password'
                                    name='rePassword'
                                    value={rePassword}
                                    onChange={this.handleChange}
                                />
                                <Form.Input
                                    fluid
                                    icon='phone'
                                    iconPosition='left'
                                    type='number'
                                    placeholder="S in thoi"
                                    name='phone'
                                    value={phone}
                                    onChange={this.handleChange}
                                />
                                <Form.Input
                                    fluid
                                    icon='content'
                                    iconPosition='left'
                                    placeholder='H v tn'
                                    name='name'
                                    value={name}
                                    onChange={this.handleChange}
                                />
                                <Form.Select
                                    fluid
                                    placeholder='Chn vai tr...'
                                    options={roleOptions}
                                    name='role'
                                    onChange={this.handleChange}
                                />
                                <Message success>
                                    Yu cu to ti khon s c gi n ngi qun tr v xc nhn.
                                </Message>
                                <Button primary fluid size='large' onClick={this.onSubmit}>
                                    ng k
                                </Button>
                            </Segment>
                        </Form>
                    </Grid.Column>
                </Grid>
                <Loader active={this.state.redirectLoading} />
            </div>
        )
    }
}

export default withRouter(RegisterScreen)
