import React, {Component} from 'react'
import {Button, Checkbox, Form, Grid, Image, Loader, Message, Segment} from 'semantic-ui-react'
import {SERVER_FILES, SERVER_API} from '../../../config'
import {withRouter} from 'react-router-dom'
import {getToken, getUserInfo, userCallWithData, getRole} from "../../../utils/ApiUtils";
import SimpleAppBar from "../exam/list/SimpleAppBar";

const bodyStyle = {
    width: '80%',
    display: 'block',
    margin: '10px auto'
}

class UserProfile extends Component {
    constructor(props) {
        super(props)
        this.handleChange = this.handleChange.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
        this.state = {
            passwordEditMode: false,
            name: '',
            email: '',
            phone: '',
            password: '',
            rePassword: '',
            loading: false,
            error: '',
            success: false
        }
    }

    handleChange(e, {name, value}) {
        this.setState({
            [name]: value
        })
    }

    setError(error) {
        this.setState({
            error
        })
    }

    setLoading(loading) {
        this.setState({
            loading
        })
    }

    updateUserInfo() {
        const {name, email, phone, password} = this.state
        this.setLoading(true)
        userCallWithData(
            'PUT',
            `${SERVER_API}/users`,
            {
                name,
                email,
                phone,
                password: password || undefined
            },
            data => this.setState({
                success: true
            }),
            err => this.setError(err),
            err => this.setError(err),
            () => this.setLoading(false)
        )
    }

    onSubmit() {
        const {password, rePassword} = this.state
        if (password !== rePassword) {
            this.setState({
                error: 'Mt khu khng khp!'
            })
            return
        }
        this.updateUserInfo()
    }

    componentDidMount() {
        this.setLoading(true)
        getUserInfo(
            data => this.setState({
                ...data
            }),
            err => {
                this.props.history.push('/login')
            },
            () => this.setLoading(false)
        )
    }

    render() {
        const token = getToken()

        const {
            _id: userId,
            role,
            loading,
            error,
            success,
            email,
            name,
            phone,
            passwordEditMode,
            password,
            rePassword
        } = this.state

        if (token) {
            if (!userId) {
                return (
                    <div>
                        <SimpleAppBar header={'Thng tin c nhn'}/>
                        <Loader size='massive' active={loading}>
                            Loading
                        </Loader>
                    </div>
                )
            }
            return (
                <div>
                    <SimpleAppBar header={'Thng tin c nhn'}/>
                    <div style={bodyStyle}>
                        <Grid centered>
                            <Grid.Column width={4}>
                                <Segment>
                                    <Image centered src={`${SERVER_FILES}/profile.png`}/>
                                    <div style={{textAlign: 'center'}}>
                                        <b>{'Vai tr: '}</b>{getRole(role)}
                                    </div>
                                </Segment>
                            </Grid.Column>
                            <Grid.Column width={12}>
                                <Form error={error !== ''} loading={loading}
                                      success={success}>
                                    <Form.Input
                                        label="Email"
                                        name="email"
                                        readOnly
                                        value={email}
                                    />
                                    <Form.Input
                                        label="Tn"
                                        name="name"
                                        value={name}
                                        onChange={this.handleChange}
                                    />
                                    <Form.Input
                                        label="S in thoi"
                                        name="phone"
                                        type={'number'}
                                        value={phone}
                                        onChange={this.handleChange}
                                    />
                                    <Checkbox
                                        toggle
                                        label='i mt khu'
                                        checked={passwordEditMode}
                                        onChange={(e, data) => this.setState({passwordEditMode: data.checked})}
                                    />
                                    <Form.Input
                                        disabled={!passwordEditMode}
                                        style={{marginTop: '10px'}}
                                        label="Mt khu mi"
                                        name="password"
                                        type='password'
                                        value={password}
                                        onChange={this.handleChange}
                                    />
                                    <Form.Input
                                        disabled={!passwordEditMode}
                                        label="Nhp li mt khu"
                                        name="rePassword"
                                        type='password'
                                        value={rePassword}
                                        onChange={this.handleChange}
                                    />
                                    <Message
                                        error
                                        header="Error"
                                        content={error}
                                    />
                                    <Message
                                        success
                                        header="Successfully"
                                        content="Thay i thng tin c nhn thnh cng"/>
                                    <Button primary type='submit' onClick={this.onSubmit}>Cp nht</Button>
                                </Form>
                            </Grid.Column>
                        </Grid>
                    </div>
                </div>
            )
        }
        return (
            <div>
                <SimpleAppBar header={'Thng tin c nhn'}/>
                <Message
                    error
                    size='massive'
                    content="ng nhp  xem thng tin c nhn!"/>
            </div>
        )
    }
}

export default withRouter(UserProfile)
